<template>
  <div id="cs-pra">
    <head-top title="随堂练习"></head-top>
    <div class="main">
      <div class="content">
        <div
          class="tt"
        >{{currentIndex + '.【' + getPracticeTypeName(practiceInfo['PSType']) + '】' + (practiceInfo['PSScore']||0) + '分'}}</div>

        <div class="ct">{{practiceInfo['PSContent']}}</div>
        <div v-if="practiceInfo['PSType']===1 || practiceInfo['PSType']===2 " class="ans">
          <div class="an" v-for="(item, k) in practiceOptions" :key="k">
            <span>{{ item['SOSerial']}}：</span>
            <span>{{item['SOContent']}}</span>
            <!-- {{ item['SOSerial'] +'：'+ item['SOContent']}} -->
          </div>
        </div>
      </div>

      <do-answer
        v-if="showDoAnswer"
        :question-type="practiceInfo['PSType']"
        :question-option="practiceInfo['PSOption']"
        :ableReply="ableReply"
        :answer-value="practiceAnswer"
        :module-picture="modulePicture"
        @change="answerChange"
      ></do-answer>
      <my-answer
        v-if="showMyAnswer"
        :question-type="practiceInfo['PSType']"
        :my-answer="practiceAnswer"
        :module-picture="modulePicture"
        :analysis="practiceInfo['PSanalysis']"
        :true-answer="practiceInfo['PSAnswer']"
        :get-score="practiceReply? practiceReply['PAScore'] : null"
      ></my-answer>

      <survey
        ref="survey"
        v-if="showSurvey"
        :disable="practiceInfo['State'] !== 4"
        :master="surveyMaster"
        @selectSurvey="selectSurvey"
      ></survey>

      <div class="footer" v-show="ableSwitch">
        <button v-show="currentIndex> 1" @click="submitAnswer(-1)">上一题</button>
        <button v-if="currentIndex< total" @click="submitAnswer(1)">下一题</button>
        <button v-else-if="ableReply && state===2 " @click="submitAnswer(1)">提交答案</button>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import DoAnswer from "./components/DoAnswer";
import MyAnswer from "./components/MyAnswer";
import Survey from "./components/Survey";

import { practiceTypeName, ModulePicture, PracticeFunc } from "@/utils/enum";
export default {
  name: "",
  components: { headTop, DoAnswer, MyAnswer, Survey },
  props: {},
  data() {
    return {
      fromID: this.$route.params.id,
      bankID: this.$route.params.bankId,
      practiceID: "",
      operateType: PracticeFunc.start, //操作步骤 1开始答题 2获取提问和答题 3下发答案和解析 4调研
      practiceInfo: {},
      practiceOptions: [],
      practiceReply: {},
      total: parseInt(this.$route.params.total || "1"),
      practiceAnswer: "",
      modulePicture: ModulePicture.PractiseAnswer,
      ableReply: false,
      currentIndex: 1,
      state: parseInt(this.$route.params.state || 1),
      ableSwitch: true,
      isSurvey: false
    };
  },
  created() {
    // 从其它页面进入， 当前界面为更新操作
    if (!this.$route.params || !this.$route.params.state) {
      this.operateType =
        this.$store.getters.classPractice.type || PracticeFunc.start;
      this.currentIndex = this.$store.getters.classPractice.no || 1;
      this.total = parseInt(
        this.$store.getters.classPractice.total || this.currentIndex
      );

      if (this.$store.getters.classPractice.type === PracticeFunc.start) {
        this.state = 2;
      }
    }
    this.getDataInfo();
  },
  methods: {
    getDataInfo() {
      Object.keys(this.practiceInfo).forEach(item => {
        this.$delete(this.practiceInfo, item);
      });
      Object.keys(this.practiceReply).forEach(item => {
        this.$delete(this.practiceReply, item);
      });
      this.practiceOptions = [];
      this.$gatewayHttp.classPractise
        .getDataByIndex({
          studentID: this.$store.getters.studentID,
          cpid: this.fromID,
          plid: this.bankID,
          psSerial: this.currentIndex
        })
        .then(res => {
          if (res.Result) {
            this.practiceInfo = res.PSData;
            this.practiceReply = res.PAData ? res.PAData : {};
            this.isSurvey = res["IsSurvey"] || false;
            this.practiceAnswer = this.practiceReply["PAContent"];

            this.ableReply = this.practiceInfo && this.state === 2; //必须答题状态为2

            this.practiceOptions = [];
            if (res.OSData && res.OSData.length > 0) {
              res.OSData.forEach(item => {
                let index = this.practiceOptions.findIndex(
                  r => JSON.stringify(r) === JSON.stringify(item)
                );
                if (index === -1) this.practiceOptions.push(item);
              });
            }
          }
        })
        .catch(err => {});
    },
    getDataInfoById() {
      this.practiceInfo = {};
      this.practiceReply = {};
      this.practiceOptions = [];
      this.$gatewayHttp.classPractise
        .getData({
          studentID: this.$store.getters.studentID,
          psID: this.practiceID,
          cpID: this.fromID
        })
        .then(res => {
          if (res.Result) {
            this.practiceInfo = res.PSData;
            this.practiceReply = res.PAData ? res.PAData : {};
            this.practiceAnswer = this.practiceReply["PAContent"];
            this.ableReply =
              this.practiceReply && this.practiceReply["PAContent"]
                ? false
                : true;
            if (res.OSData && res.OSData.length > 0) {
              res.OSData.forEach(item => {
                this.practiceOptions.push(item);
              });
            }
          }
        })
        .catch(err => {});
    },
    getPracticeTypeName(val) {
      return practiceTypeName(val);
    },
    nextTo() {
      this.currentIndex += 1;
    },
    backTo() {
      this.currentIndex -= 1;
    },
    answerChange(answer) {
      this.practiceAnswer = answer;
    },
    submitAnswer(val) {
      // if (val < 0) {
      //   this.practiceAnswer = "";
      //   this.currentIndex += val;
      //   this.getDataInfo();
      //   return;
      // } else
       if (!this.ableReply) {
        if (this.currentIndex >= this.total) {
          this.$router.push("/quiz");
        }
        this.currentIndex += val;
        this.practiceAnswer = "";
        this.getDataInfo();
        return;
      }
      if (!this.practiceAnswer) {
        this.$toast("请先作答");
      } else {
        let reqData = {
          cpid: this.fromID,
          paid: this.practiceReply["PAID"] || "",
          courseID: this.$store.getters.courseID,
          psID: this.practiceInfo["PSID"],
          studentID: this.$store.getters.studentID,
          paContent: this.practiceAnswer
        };
        this.$gatewayHttp.classPractise
          .postDataAnswer(reqData)
          .then(res => {
            if (res.Result) {
              if (this.currentIndex === this.total && val > 0) {
                if (this.state === 4) {
                  this.$router.push("/quiz");
                } else {
                  this.submitPractice();
                }
              } else {
                this.currentIndex += val;
                this.practiceAnswer = "";
                if (this.currentIndex > this.total) this.$router.push("/quiz");
                this.getDataInfo();
              }
            } else if (res.Info) {
              this.$toast(res.Info);
            }
          })
          .catch(err => {
            this.$toast("提交失败，请重试");
          });
      }
    },
    submitPractice() {
      let reqData = {
        // courseID: this.$store.getters.courseID,
        cpID: this.$route.params.id,
        studentID: this.$store.getters.studentID
      };

      this.$gatewayHttp.classPractise
        .postDataPractise(reqData)
        .then(res => {
          if (res.Result) {
            this.$toast.success("交卷成功");
            setTimeout(() => {
              this.$router.push("/quiz");
            }, 500);
          }
        })
        .catch(err => {
          this.$toast("提交失败，请重试");
        });
    },
    selectSurvey(val) {
      let master = val ? 1 : 0;
      this.$gatewayHttp.classPractise
        .postDataSurvey({
          paid: this.practiceReply["PAID"],
          studentID: this.$store.getters.studentID,
          saMaster: master
        })
        .then(res => {
          this.$set(this.practiceReply, "PAMaster", master);
          this.$refs["survey"].resetVal(master);
          this.$toast.success("提交问卷成功");
        })
        .catch(err => {
          this.$toast("提交失败，请重试");
        });
    }
  },
  computed: {
    showDoAnswer() {
      if (this.operateType === PracticeFunc.start) {
        return this.practiceInfo && this.state === 2;
      } else {
        return false;
      }
    },
    showMyAnswer() {
      if (this.operateType === PracticeFunc.start) {
        return this.practiceInfo && [3, 4].indexOf(this.state) > -1;
      } else {
        return true;
      }
    },
    showSurvey() {
      return this.isSurvey;
      // // 没有作答 没权限调研
      // if (!this.practiceAnswer) {
      //   return false;
      // } else if (this.operateType === PracticeFunc.survey) {
      //   return true;
      // } else if (this.operateType === PracticeFunc.start) {
      //   return this.practiceInfo && [3, 4].indexOf(this.state) > -1;
      // // } else if (this.isSurvey) {
      // //   return true;
      // } else {
      //   return false;
      // }
    },
    surveyMaster() {
      if (this.practiceReply) {
        return this.practiceReply["PAMaster"] === null
          ? null
          : this.practiceReply["PAMaster"] || 0;
      }
      return null;
    }
  },
  watch: {
    "$store.getters.classPractice"(newVal, oldVal) {
      // console.log("newVal:", newVal);
      if (newVal !== oldVal) {
        this.operateType = newVal.type || PracticeFunc.start;

        if (this.operateType === PracticeFunc.start) {
          this.fromID = newVal.id || "";
          this.currentIndex = newVal.no || 1;
          this.state = 2; //开始答题，肯定可以答题
          this.total = newVal.total || this.currentIndex;
          this.getDataInfo();
          this.ableSwitch = true;
        } else if (this.operateType === PracticeFunc.init) {
          this.fromID = newVal.id || "";
          this.currentIndex = newVal.no || 1;

          this.state = 4;
          this.total =
            this.total > this.currentIndex ? this.total : this.currentIndex;
          this.getDataInfo();
          this.ableSwitch = false;
        } else {
          this.practiceID = newVal.id || "";
          this.getDataInfoById();
          this.ableSwitch = false;
        }

        //  console.log("operateStep:", this.operateType);
        if (
          this.operateType === PracticeFunc.survey ||
          this.operateType === PracticeFunc.acceptAnswer
        ) {
          this.isSurvey = this.operateType === PracticeFunc.survey;

          this.$nextTick(() => {
            let height = document.body.clientHeight;
            document.documentElement.scrollTop = height;
            setTimeout(() => {
              document.documentElement.scrollTop = height;
            }, 1000);
          });
        }
      }
    }
  }
};
</script>

<style lang='less' scoped>
#cs-pra {
  width: 100%;
  height: 100%;
  .main {
    text-align: left;
    .content {
      width: 92vw;
      background: rgba(255, 255, 255, 1);
      padding: 4vw;
      color: #333333;
      font-size: 1.4rem;
      margin-bottom: 2vh;
      word-break: break-all;
      .tt {
        margin-bottom: 15px;
      }
      .ct {
        text-indent: 2em;
        margin-bottom: 15px;
      }
      .ans {
        padding-left: 10px;
        .an {
          display: flex;
          margin-bottom: 10px;
        }
      }
    }

    .footer {
      display: flex;
      justify-content: space-around;
      padding-top: 2rem;
      button {
        background: rgba(0, 203, 171, 1);
        border-radius: 5px;
        font-size: 1.3rem;
        line-height: 3;
        color: #fff;
        padding: 0 1.5rem;
      }
    }
  }
}
</style>


