<template>
  <div>
    <!-- 单选题 -->
    <div class="selects" v-if="questionType===1">
      <div class="content">
        <div class="prompt">请选择正确的答案</div>
        <div class="all-select">
          <div
            class="select-item"
            v-for="(item, i) in selectOptions"
            :key="i"
            :class="answerValue === item? 'singel-selected':''"
            @click="selectAnswer(item)"
          >{{item}}</div>
        </div>
      </div>
    </div>
    <!-- 多选题 -->
    <div class="selects" v-else-if="questionType===2">
      <div class="content">
        <div class="prompt">请选择正确的答案</div>
        <div class="all-select">
          <div
            class="select-item"
            v-for="(item, i) in selectOptions"
            :key="i"
            :class="answerValue.indexOf(item) > -1 ? 'singel-selected' : ''"
            @click="selectAnswer(item)"
          >{{item}}</div>
        </div>
      </div>
    </div>

    <!-- 判断题 -->
    <div class="answers" v-else-if="questionType===3">
      <div class="tt">请选择正确的答案</div>
      <div class="buttons">
        <div
          v-for="(item, i) in selectOptions"
          :key="i"
          :class="answerValue === item ? 'selected' : ''"
          @click="selectAnswer(item)"
        >{{item}}</div>
      </div>
    </div>

    <!-- 问答题 -->
    <div class="answers" v-else>
      <div class="tt">请将您的答案写在下方的编辑框中</div>

      <r-quill-editor
        :funType="modulePicture"
        :content="selectValue"
        @change="editorChange"
        max-height="40vh"
      ></r-quill-editor>
    </div>
    <slot></slot>
  </div>
</template>
<script>
import { Icon, Uploader } from "vant";
import RQuillEditor from "@/components/RQuillEditor";

import { newsetDesc } from "@/utils/index";
export default {
  components: { [Uploader.name]: Uploader, [Icon.name]: Icon, RQuillEditor },
  props: {
    questionType: {
      type: Number,
      default: 1
    },
    questionOption: {
      type: Number,
      default: 0
    },
    btnName: {
      type: String,
      default: "提交"
    },
    ableReply: {
      type: Boolean,
      default: true
    },
    modulePicture: {
      type: Number,
      default: 1
    },
    answerValue: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      // singleChoice: ["A", "B", "C", "D"],
      // multiChoice: ["A", "B", "C", "D"],
      judgeChoice: ["对", "错"],
      multiValue: this.selectValue ? this.selectValue.split("") : [],
      selectValue: this.answerValue
    };
  },
  mounted() {},
  methods: {
    selectAnswer(item) {
      if (!this.ableReply) return;
      if (this.questionType === 2) {
        let index = this.multiValue.indexOf(item);
        if (index > -1) {
          this.multiValue.splice(index, 1);
        } else {
          this.multiValue.push(item);
        }
        this.selectValue = this.multiValue.sort().join("");
      } else {
        this.selectValue = item;
      }
      this.backValue();
    },
    editorChange(value) {
      this.selectValue = value;
      this.backValue();
    },
    backValue() {
      this.$emit("change", this.selectValue);
    }
  },
  computed: {
    selectOptions() {
      if (this.questionType === 1) {
        return newsetDesc(this.questionOption);
      } else if (this.questionType === 2) {
        return newsetDesc(this.questionOption);
      } else if (this.questionType === 3) {
        return this.judgeChoice;
      }
    }
  },
  watch: {
    answerValue(newVal, oldVal) {
      if (newVal !== this.selectValue) {
        this.selectValue = newVal;
        this.multiValue = this.selectValue ? this.selectValue.split("") : [];
      }
    }
  }
};
</script>

<style lang='less' scoped>
.selects {
  overflow: hidden;
  width: 100vw;
  padding: 2vh 0;
  background: rgba(255, 255, 255, 1);
  margin-bottom: 2vh;
  .content {
    width: 92vw;
    margin: 0 auto;
  }
  .prompt {
    text-align: left;
    font-size: 1.2rem;
    font-family: MicrosoftYaHei;
    color: rgba(51, 51, 51, 1);
  }
  .all-select {
    display: flex;
    justify-content: space-between;
    margin-top: 16px;
    .select-item {
      width: 40px;
      height: 40px;
      background: rgba(221, 228, 227, 1);
      display: inline-block;
      line-height: 40px;
      text-align: center;
    }
    .singel-selected {
      color: #fff;
      background: rgb(24, 149, 233);
    }
  }
}

.answers {
  width: 100%;
  padding: 4vw 0;
  background: rgba(255, 255, 255, 1);

  .quill-editor {
    background: #ebf0ef;
    margin-bottom: -4vw;
  }
  .tt {
    font-size: 1.2rem;
    color: #333333;
    margin-left: 4vw;
    margin-bottom: 4vw;
    .answer_delete {
      // right: 0.2rem;
      top: 0.2rem;
    }
  }

  .buttons {
    display: flex;
    justify-content: space-around;
    div {
      width: 100px;
      height: 40px;
      background: rgba(221, 228, 227, 1);
      border-radius: 3px;
      font-size: 15px;
      font-family: MicrosoftYaHei;
      font-weight: 400;
      color: #333;
      text-align: center;
      line-height: 40px;
    }
    .selected {
      color: #fff;
      background: rgb(24, 149, 233);
    }
  }
}
</style>


